import React, { useEffect } from "react";
import LazyLoad from "react-lazyload";
import { Skeleton } from "@material-ui/lab";
import { addCartItem, removeCartItem } from "../action/userAction";
import { useDispatch, useSelector } from "react-redux";
import $ from "jquery";
import { useTranslation, Trans } from "react-i18next";

export const DetailCoursePrice = ({pic,price,id}) => {
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const dispatch = useDispatch();
    const { t } = useTranslation("translation",{useSuspense:false});
    const addCartHandler = (courseId) => {
        dispatch(addCartItem(courseId));
    };
    const removeCartItemHandler = (id) => {
        dispatch(removeCartItem(id));
    };
    useEffect(() => {
        const scrollFuc = () => {
            if ($(window).scrollTop() >= $("div.fullwitdh").height() + 100) {
                $("#right").css("position", "fixed");
                $("#right").css("margin-top", "100px");
                $("img.course-image").hide("slow", function () {});
            } else {
                $("#right").css("position", "absolute");
                $("#right").css("margin-top", "0px");
                $("img.course-image").show("slow", function () {});
            }
        }
        $(window).scroll(scrollFuc);
        return () => {
            $(window).unbind('scroll',scrollFuc);
        };
    }, []);
    useEffect(() => {
        $(".heart").hover(function () {
            $("#hearts").removeClass("far fa-heart heartss");
            $("#hearts").addClass("fas fa-heart hearted");
        });
        $(".heart").mouseout(function () {
            $("#hearts").removeClass("fas fa-heart heartss");
            $("#hearts").addClass("far fa-heart hearted");
        });
        return () => {
            $(".heart").unbind();
        };
    }, []);
    return (
        <div className="col-4 d-none d-md-block">
            <div
                className="jsright right-col-content"
                id="right"
                style={{ width: 354, left: "auto", top: 10, position: "absolute" }}
            >
                <div className="right-module">
                    <div className="image-course">
                        {pic ? (
                            <LazyLoad height={140} throttle={1000}>
                                <img
                                    className="course-image"
                                    src={pic}
                                    alt=""
                                    style={{ width: "100%", height: 140 }}
                                />
                            </LazyLoad>
                        ) : (
                            <Skeleton variant="rect" animation="wave" style={{ width: "100%", height: 140 }} />
                        )}
                    </div>
                    <div className="right-content">
                        <div className="Detail-floating">
                            <div className="buy">
                                <div className="price">
                                    <div className="now-price">
                                        <span className="course-price-now">{price} VND</span>
                                    </div>
                                    <div className="non-purchase">
                                        <span className="heart">
                                            <i id="hearts" className="far fa-heart heartss"></i>
                                        </span>
                                    </div>
                                </div>
                                <div className="confirm-buy">
                                    {userInfo ? (
                                        userInfo.cart.includes(id) ? (
                                            <div className="button-add-to-cart" style={{ marginTop: 10 }}>
                                                <button
                                                    type="button"
                                                    className="btn btn-danger"
                                                    style={{
                                                        width: "100%",
                                                        height: 50,
                                                        backgroundColor: "#4CAF50",
                                                    }}
                                                    onClick={() => {
                                                        removeCartItemHandler(id);
                                                    }}
                                                >
                                                    <Trans t={t}>Bỏ ra khỏi giỏ hàng</Trans>
                                                </button>
                                            </div>
                                        ) : (
                                            <div className="button-add-to-cart" style={{ marginTop: 10 }}>
                                                <button
                                                    type="button"
                                                    className="btn btn-danger"
                                                    style={{ width: "100%", height: 50 }}
                                                    onClick={() => {
                                                        addCartHandler(id);
                                                    }}
                                                >
                                                    <Trans t={t}>Thêm vào giỏ hàng</Trans>
                                                </button>
                                            </div>
                                        )
                                    ) : (
                                        <div className="button-buy-now" style={{ marginTop: 10 }}>
                                            <button
                                                type="button"
                                                id="login2"
                                                className="btn btn-primary"
                                                style={{ width: "100%", height: 50 }}
                                            >
                                                <Trans t={t}>Đăng nhập để thêm vào giỏ hàng</Trans>
                                            </button>
                                        </div>
                                    )}
                                </div>
                            </div>
                            <div className="includes">
                                <ul className="list">
                                    <li>
                                        <i className="far fa-clock"></i>
                                        <span><Trans t={t}>20h học</Trans></span>
                                    </li>
                                    <li className="list-item">
                                        <i className="far fa-file"></i>
                                        <span>150 <Trans t={t}>bài viết</Trans></span>
                                    </li>
                                    <li className="list-item">
                                        <i className="fas fa-heartbeat"></i>
                                        <span><Trans t={t}>Truy cập trọn đời</Trans></span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
